@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  font-family: "Noto Sans", sans-serif;
  scroll-behavior: smooth;
}
a {
  text-decoration: none;
  color: black;
}
li {
  list-style: none;
}
img {
  width: 100%;
}

header{position:fixed;left:0;top:0;width:100%;background:#fff;z-index:11;box-shadow:2px 2px 10px rgba(0,0,0,0.1)}.navbar{max-width:1180px;padding:0 20px;margin:0 auto;display:flex;align-items:center;justify-content:space-between;height:70px}@media (max-width: 992px){.navbar{justify-content:start}}.navbar__logo{max-width:110px;cursor:pointer}.navbar__menu{display:flex;align-items:center}@media (max-width: 992px){.navbar__menu{position:fixed;right:0;top:0;max-width:400px;width:100%;min-height:100vh;background:#fff;display:flex;flex-direction:column;padding:60px 20px;z-index:10;text-align:center;box-shadow:-3px -3px 10px rgba(0,0,0,0.1);-webkit-animation:fadeRight 0.2s ease;animation:fadeRight 0.2s ease}@-webkit-keyframes fadeRight{0%{right:-100%}100%{right:0}}@keyframes fadeRight{0%{right:-100%}100%{right:0}}}.navbar__menu-moreIcon{cursor:pointer;margin-top:5px;position:relative}@media (max-width: 992px){.navbar__menu-moreIcon{margin-top:0}}.navbar__menu-more{position:absolute;left:0;top:100%;border:1px solid #ccc;border-radius:3px;width:-webkit-max-content;width:max-content;min-width:158px;-webkit-animation:fadeIn 0.1s linear;animation:fadeIn 0.1s linear;background:#fff}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.navbar__menu-more li{padding:10px 14px}.navbar__menu-more li:hover{background:#f5f5f5;transition:0.2s}.navbar__item{font-size:14px;padding:0 15px}.navbar__item:hover a{color:#0288d1;transition:0.2s linear}@media (max-width: 992px){.navbar__item{font-size:17px;padding:5px 0}}.navbar__search{cursor:pointer;margin:0 15px}@media (max-width: 992px){.navbar__search{margin-left:auto}}.navbar__menuIcon{display:none}@media (max-width: 992px){.navbar__menuIcon{display:block;margin-left:15px;cursor:pointer;z-index:999}}

.user{display:flex;align-items:center;cursor:pointer;position:relative}.user__btn{display:flex;align-items:center}.user__btn-icon{font-size:30px}.user__btn-iconDown{font-size:20px}.user__btn-name{margin:0 5px}.user__menu{position:absolute;right:0;top:130%;border:1px solid #ccc;border-radius:3px;background:#fff;-webkit-animation:fadeIn 0.1s linear;animation:fadeIn 0.1s linear;width:-webkit-max-content;width:max-content}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.user__menu-item{padding:5px 10px}.user__menu-item.red{color:#dc4036}.user__menu-item:hover{background:#f5f5f5;transition:0.2s ease}

.formContainer{width:100%;min-height:100vh;padding-top:100px;background:#f2f2f2;display:flex;justify-content:center}.formContainer .form{max-width:600px;width:100%;padding:35px 30px;background:#fff;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;box-shadow:3px 3px 10px 1px rgba(0,0,0,0.1);-webkit-animation:show 0.5s ease;animation:show 0.5s ease}@-webkit-keyframes show{from{-webkit-transform:translateY(-100%);transform:translateY(-100%);opacity:0}to{-webkit-transform:translateY(0);transform:translateY(0);opacity:1}}@keyframes show{from{-webkit-transform:translateY(-100%);transform:translateY(-100%);opacity:0}to{-webkit-transform:translateY(0);transform:translateY(0);opacity:1}}.formContainer .form__title{color:#464646;text-align:center;font-size:25px;margin-bottom:30px}.formContainer .form__groups{display:flex;grid-gap:15px;gap:15px;margin-bottom:-8px}.formContainer .form__groups div{flex:1 1}@media screen and (max-width: 600px){.formContainer .form__groups{flex-direction:column;grid-gap:0;gap:0}}.formContainer .form__group{width:100%;position:relative;height:45px;border:1px solid #464646;border-radius:3px;display:flex;flex:1 1;align-items:center;padding-left:10px;margin:14px 0px 8px 0px}.formContainer .form__group input{height:43px;flex:1 1;padding:10px;border:none;outline:none;background:none;border-radius:3px}.formContainer .form__group select{height:43px;border:none;width:100%;outline:none;margin-right:5px}.formContainer .form__group-icon{color:#464646;margin-right:10px}.formContainer .form__group-icon2{color:#464646;margin-right:10px;font-size:18px;cursor:pointer}.formContainer .form__error{font-size:13px;color:#dc4036}.formContainer .form__checkbox{display:flex;justify-content:space-between;font-size:14px;margin-bottom:15px}.formContainer .form__checkbox label{display:flex;align-items:center}.formContainer .form__checkbox p{display:inline-block;margin-left:10px;cursor:pointer}.formContainer .form__checkbox a{color:#0288d1}.formContainer .form__btn{width:100%;height:45px;background:#0288d1;color:#fff;margin-bottom:35px;border:none;outline:none;border-radius:3px;font-size:16px;font-weight:700;cursor:pointer;transition:opacity 0.2s ease}.formContainer .form__btn:active{opacity:0.8}.formContainer .form__signUp{font-size:14px;color:#464646;margin-bottom:15px;margin-top:20px;text-align:center}.formContainer .form__signUp a{color:#0288d1}.formContainer .form__desc{font-size:14px;color:#464646;text-align:center}

.profile{width:100%;height:100vh;display:flex;align-items:center;justify-content:center;flex-direction:column}

.home{width:100%;min-height:100vh;padding-top:70px}.home__container{max-width:1180px;padding:0 20px;margin:0 auto}.home__title{text-align:center;font-size:35px;font-weight:700;margin-bottom:40px;margin-top:30px}.home__products{display:grid;grid-template-columns:repeat(5, 1fr);grid-gap:20px}@media screen and (max-width: 1100px){.home__products{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 900px){.home__products{grid-template-columns:repeat(3, 1fr)}}@media screen and (max-width: 700px){.home__products{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 450px){.home__products{grid-template-columns:repeat(1, 1fr)}}.home__product{height:230px;border-radius:5px;border:1px solid #ccc;overflow:hidden}.home__product:hover{cursor:pointer;transition:box-shadow 0.25s;box-shadow:0 2px 2px 0 rgba(0,0,0,0.2),0 4px 15px 0 rgba(0,0,0,0.19)}.home__product-img{height:150px;overflow:hidden;position:relative}.home__product-img img{position:absolute;left:0;top:0;width:100%;height:100%;object-fit:cover}.home__product-name{font-weight:700;padding:10px;padding-top:5px;text-overflow:ellipsis;white-space:nowrap;overflow:hidden}.home__product-price{color:#dc4036;padding:0 10px}.home__pagination{display:flex;margin:30px 0;justify-content:space-between}.home__pagination-loadmore{border:1px solid #dc4036;border-radius:3px;color:#dc4036;height:40px;padding:0 20px;background-color:transparent;outline:none;text-transform:uppercase;font-weight:700;cursor:pointer}.home__pagination-gotop{width:40px;height:40px;margin:0 10px;display:flex;align-items:center;justify-content:center;background:#dc4036;border-radius:3px;color:#fff;outline:none;border:none;cursor:pointer}

.detail{max-width:1180px;padding:0 20px;margin:0 auto;display:grid;grid-template-columns:repeat(2, 1fr);margin-top:100px}@media screen and (max-width: 800px){.detail{grid-template-columns:repeat(1, 1fr)}}.detail__img{margin-right:30px;position:relative}@media screen and (max-width: 800px){.detail__img{margin-right:0}}.detail__img>div>div{width:100% !important}.detail__img>div{background:#ccc !important}.detail__content-name{font-size:30px;font-weight:700;margin-top:10px}.detail__content-price{color:#dc4036;font-size:18px;margin:10px 0}.detail__content-desc{overflow-y:scroll;max-height:275px;margin-bottom:20px}

